.range-box {
  position: relative;

  min-height: 10px;
}

.range-box__line {
  @include opacity-bg($blue, 0.2);

  position: absolute;
  top: 50%;

  width: 100%;
  height: 2px;

  border-radius: 1px;
  transform: translateY(-50%);
}

.range-box__line_up {
  right: 0;

  width: 70%;

  background-color: $blue;
}

.range-box__button {
  position: absolute;
  top: 50%;
  z-index: 2;

  width: 10px;
  height: 10px;
  padding: 0;

  background-color: $blue;
  border: 0;
  border-radius: 50%;
  transform: translateY(-50%);

  &:hover,
  &:focus {
    width: 19px;
    height: 19px;
  }

  &:active {
    box-shadow: 0 3px 7px 0 rgba(29, 46, 91, 0.35);
  }
}

.range-box__button_min {
  left: 30%;
}

.range-box__button_max {
  right: 0;
}

@media (min-width: $tablet-width) {
  .range-box {
    width: 54.6%;
  }

  .range-box__button {
    width: 13px;
    height: 13px;
  }
}

@media (min-width: $desktop-width) {
  .range-box {
    width: auto;
  }
}
